@media print {
  .maininfo-buttons {
    display: none;
  }
  .keyword-highlight {
    background-color: none;
  }
  .file-img {
    display: none;
  }
  thead {
    display: table-header-group;
  }
  tfoot {
    display: table-footer-group;
  }
}

@page {
  margin: 12mm;
}

.maininfo-page-header {
  position: fixed;
  top: 0mm;
  width: 100%;
  border-bottom: 1px solid gray;
}
.maininfo-page-footer {
  position: fixed;
  bottom: 0mm;
  width: 100%;
  border-top: 1px solid gray;
}

ins {
  background-color: yellow;
  text-decoration: none;
}

.maininfo-law-head {
  text-align: center;
}

div[key] .searchword-highlight {
  background-color: #f12ea760;
}

.maininfo-container {
  position: fixed;
  left: 20vw;
  right: 20vw;
  width: 61vw;
  height: 80%;
  align-content: center;
  padding: 1rem;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.maininfo-container::-webkit-scrollbar {
  display: none;
}

.maininfo-law-head-info {
  color: rgb(36, 138, 179);
}

.maininfo-chapter-titles {
  color: #161694;
  font-size: x-large;
  font-weight: 600;
  line-height: 3 !important;
}

.maininfo-contents {
  line-height: 200%;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.maininfo-article-title {
  display: inline;
  color: #161694;
  padding-left: 0.5rem;
  font-size: medium;
  font-weight: 500;
}

.maininfo-addenda-title {
  display: inline;
  color: #161694;
  padding-left: 0.5rem;
  font-size: medium;
  font-weight: 500;
}

.maininfo-article-context {
  line-height: 2 !important;
}

.maininfo-buttons-pan {
  background-color: #4caf50;
  border: 1px solid #4caf50;
  color: white;
  border-radius: 4px;
  padding: 0 4px;
  margin-left: 0.3rem;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition-duration: 0.4s;
}

.maininfo-buttons-pan:hover {
  background-color: white;
  color: #4caf50;
}

.maininfo-buttons-yeon {
  background-color: #008cba;
  border: 1px solid #008cba;
  color: white;
  border-radius: 4px;
  padding: 0 4px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition-duration: 0.4s;
}

.maininfo-buttons-yeon:hover {
  background-color: white;
  color: #008cba;
}

.maininfo-buttons-hang {
  background-color: #f44336;
  border-radius: 4px;
  border: 1px solid #f44336;
  color: white;
  padding: 0 4px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.maininfo-buttons-hang:hover {
  background-color: white;
  color: #f44336;
}

.maininfo-buttons-gyu {
  background-color: #555555;
  border: 1px solid #555555;
  border-radius: 4px;
  color: white;
  padding: 0 4px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.maininfo-buttons-gyu:hover {
  background-color: white;
  color: #555555;
}

div[class*='maininfo'] {
  line-height: 2 !important;
}

a {
  text-decoration: none;
  color: inherit !important;
}

.date {
  color: rgb(36, 138, 179);
}

.file-button-hwp {
  background-color: #324ad6;
  border: 1px solid #324ad6;
  color: white;
  border-radius: 4px;
  padding: 4px 8px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition-duration: 0.4s;
}

.file-button-pdf {
  background-color: #e20f32;
  border: 1px solid #e20f32;
  color: white;
  border-radius: 4px;
  padding: 4px 8px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition-duration: 0.4s;
}
